.team-section {
  padding: 80px 0;
  background: linear-gradient(180deg, #ffffff 0%, #1f376e 100%);
}

.section-header {
  text-align: center;
  margin-bottom: 60px;
}

.section-header-content {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin-bottom: 15px;
}

.section-logo {
  width: 60px;
  height: 60px;
}

.section-header h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.section-header p {
  font-size: 1.1rem;
  color: #000000;
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
}

.team-card {
  background: white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.team-card-image {
  width: 100%;
  height: 280px;
  overflow: hidden;
  background: #e0e0e0;
  position: relative;
}

.team-card-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease, filter 0.3s ease;
  filter: grayscale(70%) brightness(0.9);
}

.team-card:hover .team-card-image img {
  transform: scale(1.1);
  filter: grayscale(0%) brightness(1);
}

.team-card-content {
  padding: 25px;
  text-align: center;
}

.team-name {
  font-size: 1.5rem;
  margin-bottom: 8px;
  color: #333;
  font-weight: 600;
}

.team-profession {
  color: #355fc1;
  font-weight: 600;
  margin-bottom: 5px;
  font-size: 0.95rem;
}

.team-role {
  color: #666;
  font-size: 0.9rem;
  margin-bottom: 15px;
}

.team-social {
  display: flex;
  justify-content: center;
  gap: 15px;
}

.team-social a {
  color: #6c63ff;
  font-size: 1.5rem;
  transition: color 0.3s ease;
  text-decoration: none;
}

.team-social a:hover {
  color: #5a52d5;
}
