.space-section {
  padding: 100px 0;
  background: linear-gradient(180deg, #ffffff 0%, #1f376e 100%);
  position: relative;
  overflow: hidden;
}

.space-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 20% 50%, rgba(108, 99, 255, 0.1) 0%, transparent 50%),
              radial-gradient(circle at 80% 80%, rgba(99, 179, 237, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

.space-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  z-index: 1;
}

.space-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  align-items: center;
  margin-bottom: 60px;
  margin-left: 4rem;
}

.space-text h2 {
  font-size: 3rem;
  margin-bottom: 15px;
  color: #fff;
}

.space-subtitle {
  font-size: 1.3rem;
  color: #000000;
  font-weight: 600;
  margin-bottom: 20px;
}

.space-description {
  font-size: 1.1rem;
  color: #000000;
  line-height: 1.8;
  margin-bottom: 20px;
}

.space-visual {
  display: flex;
  justify-content: center;
  align-items: center;
  perspective: 1000px;
}

.space-logo-container {
  position: relative;
  width: 250px;
  height: 250px;
  background: linear-gradient(135deg, #667eea 0%, #1f376e 100%);
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(10px);
  box-shadow: 0 8px 32px 0 rgba(54, 108, 178, 0.2);
  animation: float-animation 3s ease-in-out infinite;
}

.space-logo {
  width: 150px;
  height: 150px;
  filter: drop-shadow(0 0 20px rgba(54, 108, 178, 0.5));
}

.space-image-container {
  position: relative;
  border-radius: 20px;
  overflow: hidden;
  height: 600px;
  box-shadow: 0 20px 60px rgba(54, 108, 178, 0.3);
}

.space-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.6s ease;
}

.space-image-container:hover .space-image {
  transform: scale(1.05);
}

.space-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(108, 99, 255, 0.2) 0%, rgba(99, 179, 237, 0.2) 100%);
  pointer-events: none;
}

/* Responsive */
@media (max-width: 768px) {
  .space-content {
    grid-template-columns: 1fr;
    gap: 40px;
  }

  .space-text h2 {
    font-size: 2rem;
  }

  .space-image-container {
    height: 300px;
  }

  .space-logo-container {
    width: 200px;
    height: 200px;
  }

  .space-logo {
    width: 120px;
    height: 120px;
  }
}
